<template>
	<div class="home">
		<div class="row">
			<transition name="translate"><div class="item" v-show="show">
			<router-link to="/index/xmgk"><img src="../assets/index_menu_01.png"/></router-link>
		</div></transition>
		<transition name="translate"><div class="item" v-show="show">
			<router-link to="/index/pmqw"><img src="../assets/index_menu_02.png"/></router-link>
		</div></transition>
		<transition name="scale"><div class="item" v-show="show">
			<router-link to="/index/xmjz"><img src="../assets/index_menu_03.png"/></router-link>
		</div></transition>
		</div>
		<div class="row">
			<transition name="scale"><div class="item" v-show="show">
			<img src="../assets/index_menu_04.png"/>
		</div></transition>
		<transition name="translate"><div class="item" v-show="show">
			<router-link to="/index/hxsx"><img src="../assets/index_menu_05.png"/></router-link>
		</div></transition>
		<transition name="translate"><div class="item" v-show="show">
			<img src="../assets/index_menu_06.png"/>
		</div></transition>
		</div>
	</div>
</template>

<script>

	export default {
		name: 'home',
		data() {
			return {
				show: false
			}
		},
		mounted() {
			this.show = true
		}
	}
</script>

<style scoped>
	.home {
		height: calc(100vh - 55px);
		background: url('../assets/index_bg.jpg') no-repeat center;
		background-size: 100% 100%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.home .row {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
	}

	.home .item {
		width: 10%;
		margin: 8%;
	}
	.home .item a {
		display: block;
	}
</style>


