<template>
	<div class="pmqw" :class="{ active: isActive }">
		<div class="item item-1">
			<img src="../assets/pmqw_menu_01.png"/>
		</div>
		<div class="item item-2">
			<img src="../assets/pmqw_menu_02.png"/>
		</div>
		<div class="item item-3">
			<img src="../assets/pmqw_menu_03.png"/>
		</div>
		<div class="item item-4">
			<img src="../assets/pmqw_menu_04.png"/>
		</div>
		<div class="item item-5">
			<img src="../assets/pmqw_menu_05.png"/>
		</div>
	</div>
</template>

<script>

	export default {
		name: 'pmqw',
		data() {
			return {
				isActive: false
			}
		},
		mounted() {
			setTimeout(() => {
				this.isActive = true
			}, 300)

			setTimeout(() => {
				this.isInShow = true
			}, 800)
		}
	}
</script>

<style scoped>
	.pmqw {
		height: calc(100vh - 55px);
		background: url('../assets/qw_bg.jpg') no-repeat center;
		background-size: 100% 100%;
	}
	.item {
		position: absolute;
		width: 6%;
		transform: scale(0);
		transition: all .4s cubic-bezier(0.18, 0.89, 0.32, 1.28);
	}
	.item-1 {
		top: 23%;
		left: 25%;
	}
	.item-2 {
		top: 18%;
		left: 40%;
	}
	.item-3 {
		top: 36%;
		left: 42%;
	}
	.item-4 {
		top: 35%;
		right: 25%;
	}
	.item-5 {
		top: 52%;
		right: 2%;
	}
	.active .item {
		transform: none;
	}
</style>


