<template>
	<div class="hxsx" :class="{ active: isActive }">
		<div class="item item-1">
			<router-link to="/index/hxjs?type=0"><img src="../assets/hx_menu_01.png"/></router-link>
		</div>
		<div class="item item-2">
			<router-link to="/index/hxjs?type=1"><img src="../assets/hx_menu_02.png"/></router-link>
		</div>
		<div class="item item-3">
			<router-link to="/index/hxjs?type=2"><img src="../assets/hx_menu_03.png"/></router-link>
		</div>
	</div>
</template>

<script>

	export default {
		name: 'hxsx',
		data() {
			return {
				isActive: false
			}
		},
		mounted() {
			setTimeout(() => {
				this.isActive = true
			}, 300)
		}
	}
</script>

<style scoped>
	.hxsx {
		height: calc(100vh - 55px);
		background: url('../assets/xmjz_bg.jpg') no-repeat center;
		background-size: 100% 100%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		overflow: hidden;

	}
	.item {
		width: 60%;
		margin: 0 15px;
		transform: translateX(-100%);
		opacity: 0;
		transition: all .4s;
	}
	.item-2 {
		margin-top: 20%;
		margin-bottom: 20%;
		align-self: flex-end;
		transform: translateX(100%);
	}
	.active .item {
		transform: none;
		opacity: 1;
	}
</style>


