import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/main'
import Index from '@/components/index'
import Home from '@/components/home'


import Xmgk from '@/components/xmgk'
import Xmjs from '@/components/xmjs'
import Qyjs from '@/components/qyjs'
import Pmqw from '@/components/pmqw'
import Xmjz from '@/components/xmjz'
import Hxsx from '@/components/hxsx'
import Hxjs from '@/components/hxjs'

Vue.use(Router)

export default new Router({
  routes: [
    {
    	path: '/',
    	name: 'loader',
    	component: Main
    },
    {
    	path: '/index',
    	component: Index,
    	children: [
    	{
    		path: '',
    		component: Home
    	},
        {
            path: 'xmgk',
            component: Xmgk
        },
        {
            path: 'xmjs',
            component: Xmjs
        },
        {
            path: 'qyjs',
            component: Qyjs
        },
        {
            path: 'pmqw',
            component: Pmqw
        },
        {
            path: 'xmjz',
            component: Xmjz
        },
        {
            path: 'hxsx',
            component: Hxsx
        },
        {
            path: 'hxjs',
            component: Hxjs
        }
    	]
    }
  ]
})



