<template>
	<div class="qyjs">
		<transition name="scale"><div class="logo" v-show="isLogoShow">
			<img src="../assets/main_logo_m.png"/>
		</div></transition>
		<transition name="translate"><div class="ins" v-show="isInShow">
			<p>黄山祥龙实业集团有限公司坐落于黄山区中心城区，成立于2002年5月18日。成立十多年来，集团秉承"诚实、守信、开拓、创新"的企业精神，不断发展壮大。现已发展成为拥有十五个子公司，一个分公司和一个门球俱乐部的综合性、跨地区、跨行业的现代企业集团，其经营范围涉及地产开发、矿产资源开发、商业批发零售、旅游景点、星级酒店、建筑施工、物业管理等领域。目前，公司在职职工800余人，资产总额达十多亿元，年均营业额二亿元，年均纳税总额千万余元，在实现自身发展壮大的同时，也社会事业的发展做出了积极的贡献。</p>
			<p>公司连续五年被评为黄山市民营企业五十强，并被评为诚信企业，重合同守信用企业和税收重点贡献企业。董事长邢祥龙先生也当选为黄山市第五届政协委员、黄山区人大常委；安徽省第四届门球协会副主席、黄山市民营企业家协会常务副会长、黄山区工商联副主席。</p>
			<p>经过多年的发展，黄山祥龙集团凝聚了强大的前进动力；展望未来，祥龙集团将充分发挥已有优势，努力构建具有综合实力和核心竞争力的国内一流企业。</p>
		</div></transition>
		<transition name="fade"><div class="foot" v-show="isfootShow">
			<img src="../assets/qyjs_foot.png"/>
		</div></transition>
	</div>
</template>

<script>

	export default {
		name: 'qyjs',
		data() {
			return {
				isLogoShow: false,
				isInShow: false,
				isfootShow: false
			}
		},
		mounted() {
			setTimeout(() => {
				this.isLogoShow = true
			}, 300)

			setTimeout(() => {
				this.isInShow = true
			}, 800)

			setTimeout(() => {
				this.isfootShow = true
			}, 1000)
		}
	}
</script>

<style scoped>
	.qyjs {
		height: calc(100vh - 55px);
		background: url('../assets/qyjs_bg.jpg') no-repeat center;
		background-size: 100% 100%;

	}
	.logo {
		width: 25%;
		position: absolute;
		top: 50px;
		left: 50%;
		transform: translateX(-50%);
	}
	.ins {
		position: absolute;
		top: 25%;
		color: #331e17;
		font-size: 10px;
		padding: 0 20px;
	}
	.ins p {
		margin-bottom: 20px;
		text-indent: 20px;
	}
	.foot {
		position: absolute;
		width: 30%;
		left: 50%;
		transform: translateX(-50%);
		bottom: 40px;
	}
</style>


