<template>
	<div class="main vein">
		<div class="logo">
			<img src="../assets/main_logo_m.png"/>
		</div>
		<transition name="scale"><div class="band" v-show="isBandShow">
			<img src="../assets/main_logo.png"/>
		</div></transition>
		<transition name="fade"><div class="enter" v-show="isEnterShow">
			<router-link to="/index">
				<img src="../assets/main_enter.png"/>
			</router-link>
		</div></transition>
		<div class="foot">
			<div class="feature">
				<img src="../assets/main_feature.png"/>
			</div>
			<div class="contact">
				<img src="../assets/main_address.png"/>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'loader',
		data() {
			return {
				isBandShow: false,
				isEnterShow: false
			}
		},
		methods: {
			sync(fun, delay) {
				setTimeout(() => {
					fun()
				}, delay)
			}
		},
		mounted() {
			this.sync(() => {
				this.isBandShow = true
			}, 300)

			this.sync(() => {
				this.isEnterShow = true
			}, 800)
		}
	}
</script>

<style scoped>
	.main {
		background-image: url('../assets/main_bg.jpg');
	}
	.logo {
		position: absolute;
		width: 30%;
		top: 20px;
		left: 25px;
	}
	.band {
		position: absolute;
		width: 70%;
		max-width: 250px;
		left: 50%;
		top: 20%;
		transform: translateX(-50%);
	}
	.enter {
		width: 40%;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -100%);
	}
	.enter a {
		display: block;
	}
	.foot {
		position: absolute;
		bottom: 0;
	}
	.foot .feature {
		padding: 0 20px 0;
	}
	.foot .contact {
		padding: 15px 20px 20px;
	}
</style>


