<template>
	<div class="hxjs" :class="{ active: isActive }">
		<div class="title">
			<img :src="layoutList[$route.query.type].title"/>
		</div>
		<div class="map">
			<img :src="layoutList[$route.query.type].map"/>
		</div>
		<div class="tuli">
			<div class="sut">
				<div class="area"></div>
				<div class="label">全赠送</div>
			</div>
			<div class="sut">
				<div class="area dashed"></div>
				<div class="label">半赠送</div>
			</div>
		</div>
		<div class="menus">
			<div class="item" v-for="item in layoutList[$route.query.type].btns">
				<img :src="item | path"/>
			</div>
		</div>
	</div>
</template>

<script>

	export default {
		name: 'hxjs',
		data() {
			return {
				isActive: false,
				layoutList: [
				{
					title: './static/img/hx_menu_01.png',
					map: './static/img/layout_map_01.png',
					btns: ['dzh', 'ms', 'xd']
				},
				{
					title: './static/img/hx_menu_02.png',
					map: './static/img/layout_map_02.png',
					btns: ['hxd', 'ms', 'xzs']
				},
				{
					title: './static/img/hx_menu_03.png',
					map: './static/img/layout_map_03.png',
					btns: ['bo', 'hxd', 'xzs']
				}
				]
			}
		},
		mounted() {
			setTimeout(() => {
				this.isActive = true
			}, 300)
		},
		filters: {
			path(val) {
				return './static/img/btn_' + val + '.png'
			}
		}
	}
</script>

<style scoped>
	.hxjs {
		height: calc(100vh - 55px);
		background: url('../assets/xmjz_bg.jpg') no-repeat center;
		background-size: 100% 100%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		overflow: hidden;

	}
	.title {
		position: absolute;
		width: 50%;
		top: 20px;
		left: 50%;
		transform: translate(-50%);
	}
	.map {
		position: absolute;
		width: 85%;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
	.tuli {
		position: absolute;
		width: 100%;
		bottom: 13%;
		display: flex;
		justify-content: center;
	}
	.sut {
		display: flex;
		font-size: 12px;
		align-items: center;
		margin-right: 20px;
	}
	.sut .area {
		width: 25px;
		height: 13px;
		background: transparent;
		border-color: red;
		border-width: 1px;
		border-style: solid;
		margin-right: 4px;
	}
	.sut .dashed {
		border-style: dashed;
	}
	.menus {
		position: absolute;
		width: 100%;
		bottom: 25px;
		display: flex;
		justify-content: center;
	}
	.item {
		width: 40%;
		margin: 0 2%;
	}
</style>


