<template>
	<ul class="tool-bar">
		<li>
			<router-link to="/index">
				<i class="icon home"></i>
				<span class="label">地图导航</span>
			</router-link>
		</li>
		<li>
			<router-link to="/">
				<i class="icon news"></i>
				<span class="label">房贷计算器</span>
			</router-link>
		</li>
		<li>
			<a href="tel:10010">
				<i class="icon cart"></i>
				<span class="label">联系电话</span>
			</a>
		</li>
		<li>
			<a @click="$router.go(-1)">
				<i class="icon user"></i>
				<span class="label">返回上层</span>
			</a>
		</li>
	</ul>
</template>

<script>
	export default {
		name: 'toolbar'
	}
</script>

<style scoped>
	.tool-bar {
		display: flex;
		height: 55px;
		background: url('../../assets/tab_bar_bg.jpg') no-repeat center;
		background-size: 100% 100%;
	}
	.tool-bar li {
		list-style-type: none;
		width: 25%;
	}
	.tool-bar li a {
		display: flex;
		flex-direction: column;
		align-items: center;
		height: 100%;
		justify-content: center;
		text-decoration: none;
	}
	.tool-bar li a .label {
		font-size: 13px;
		color: #331e17;
	}
	.tool-bar .icon {
		display: inherit;
		width: 33px;
		height: 33px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 28px auto;
	}
	.tool-bar .home {
		background-image: url('../../assets/tab_bar_icon_map.png');
	}
	.tool-bar .news {
		background-image: url('../../assets/tab_bar_icon_calc.png');
		background-size: 22px auto;
	}
	.tool-bar .cart {
		background-image: url('../../assets/tab_bar_icon_tel.png');
	}
	.tool-bar .user {
		background-image: url('../../assets/tab_bar_icon_back.png');
	}
</style>


