<template>
	<div class="index">
		<div class="layout">
			<transition name="translate"><router-view class="child-view"/></transition>
		</div>
		<tool-bar></tool-bar>
	</div>
</template>

<script>
	import toolBar from './units/toolbar'

	export default {
		name: 'index',
		components: { toolBar }
	}
</script>

<style scoped>
	.index {
		top: 0;
		left: 0;
	}
	.index .layout {
		position: relative;
		height: calc(100vh - 55px);
		background-color: #f1f1f1;
		overflow-y: scroll;
	}
	.child-view {
		position: absolute;
		width: 100%;
	}
</style>s


