<template>
	<div class="xmjz">
		<transition name="scale"><div class="logo" v-show="isLogoShow">
			<img src="../assets/main_logo.png"/>
		</div></transition>
		<div class="menus">
			<div class="item">
				<img src="../assets/xmjz_menu_01.png"/>
			</div>
			<div class="item">
				<img src="../assets/xmjz_menu_02.png"/>
			</div>
		</div>
	</div>
</template>

<script>

	export default {
		name: 'xmjz',
		data() {
			return {
				isLogoShow: false
			}
		},
		mounted() {
			setTimeout(() => {
				this.isLogoShow = true
			}, 300)
		}
	}
</script>

<style scoped>
	.xmjz {
		height: calc(100vh - 55px);
		background: url('../assets/xmjz_bg.jpg') no-repeat center;
		background-size: 100% 100%;

	}
	.logo {
		width: 38%;
		position: absolute;
		top: 20px;
		left: 50%;
		transform: translateX(-50%);
	}
	.menus {
		position: absolute;
		width: 100%;
		bottom: 25px;
		display: flex;
		justify-content: center;
	}
	.item {
		width: 40%;
		margin: 0 2%;
	}
</style>


