<template>
	<div class="xmgk">
		<div class="row">
		<transition name="translate"><div class="item" v-show="show">
			<router-link to="/index/qyjs"><img src="../assets/xmgk_menu_01.png"/></router-link>
		</div></transition>
		<transition name="scale"><div class="item" v-show="show">
			<router-link to="/index/xmjs"><img src="../assets/xmgk_menu_02.png"/></router-link>
		</div></transition>
		</div>
		</div>
	</div>
</template>

<script>

	export default {
		name: 'xmgk',
		data() {
			return {
				show: false
			}
		},
		mounted() {
			this.show = true
		}
	}
</script>

<style scoped>
	.xmgk {
		height: calc(100vh - 55px);
		background: url('../assets/xmgk_bg.jpg') no-repeat center;
		background-size: 100% 100%;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.xmgk .row {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
	}

	.xmgk .item {
		width: 10%;
		margin: 8%;
	}
	.xmgk .item a {
		display: block;
	}
</style>


