<template>
	<div class="xmjs">
		<transition name="scale"><div class="band" v-show="isBandShow">
			<img src="../assets/main_logo.png"/>
		</div></transition>
		<transition name="fade"><div class="ins" v-show="isInShow">
			<p>本项目位于翡翠西路与芙蓉路西北侧，项目西侧为浦溪河，景观资源丰富，项目跑离甘棠中心学校约500米，距离太平客运中心约600米，距离凤凰湖公园和区行政办公区约1.2公里，周边交通便捷，配套设施齐全。</p>
		</div></transition>
	</div>
</template>

<script>

	export default {
		name: 'xmjs',
		data() {
			return {
				isBandShow: false,
				isInShow: false
			}
		},
		mounted() {
			setTimeout(() => {
				this.isBandShow = true
			}, 300)

			setTimeout(() => {
				this.isInShow = true
			}, 800)
		}
	}
</script>

<style scoped>
	.xmjs {
		height: calc(100vh - 55px);
		background: url('../assets/xmjs_bg.jpg') no-repeat center;
		background-size: 100% 100%;

	}
	.band {
		width: 40%;
		position: absolute;
		top: 50px;
		left: 50%;
		transform: translateX(-50%);
	}
	.ins {
		position: absolute;
		top: 25%;
		color: #331e17;
		font-size: 12px;
		padding: 0 20px;
	}
</style>


